import React, { useEffect, useState, useCallback } from 'react';
import { PublicKey, Transaction, SystemProgram } from '@solana/web3.js';

// Добавляем тип для параметров URL
interface URLParams {
  publicKey?: string;
  txSignature?: string;
  error?: string;
}

const App: React.FC = () => {
  const [publicKey, setPublicKey] = useState<PublicKey | null>(null);
  const [txSignature, setTxSignature] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  // Функция для парсинга параметров URL
  const getUrlParams = (): URLParams => {
    const params = new URLSearchParams(window.location.search);
    return {
      publicKey: params.get('publicKey') || undefined,
      txSignature: params.get('txSignature') || undefined,
      error: params.get('error') || undefined
    };
  };

  // Обработка возврата из Phantom
  useEffect(() => {
    const params = getUrlParams();
    
    if (params.publicKey) {
      try {
        setPublicKey(new PublicKey(params.publicKey));
        // Очищаем URL от параметров
        window.history.replaceState({}, document.title, window.location.pathname);
      } catch (err) {
        setError('Invalid public key');
      }
    }

    if (params.txSignature) {
      setTxSignature(params.txSignature);
      window.history.replaceState({}, document.title, window.location.pathname);
    }

    if (params.error) {
      setError(params.error);
      window.history.replaceState({}, document.title, window.location.pathname);
    }
  }, []);

  const connectWallet = useCallback(() => {
    setError(null);
    
    // Генерируем URL для возврата с публичным ключом
    const redirectUrl = new URL(window.location.href);
    redirectUrl.searchParams.append('fromPhantom', 'true');
    
    // Deep link для открытия Phantom с callback URL
    const phantomDeepLink = `phantom://browse/${encodeURIComponent(redirectUrl.toString())}`;
    
    // Fallback URL если Phantom не установлен
    const fallbackUrl = 'https://phantom.app/download';
    
    // Пытаемся открыть Phantom
    window.location.href = phantomDeepLink;
    
    // Если через 500ms мы все еще на странице, открываем fallback
    setTimeout(() => {
      if (!document.hidden) {
        window.location.href = fallbackUrl;
      }
    }, 500);
  }, []);

  const handleTransaction = useCallback(() => {
    if (!publicKey) {
      setError('Wallet not connected');
      return;
    }

    // Генерируем URL для возврата с подписью транзакции
    const redirectUrl = new URL(window.location.href);
    redirectUrl.searchParams.append('txReturn', 'true');
    
    // Здесь должна быть ваша логика создания транзакции
    // Вместо этого создаем mock транзакцию для примера
    const transaction = new Transaction().add(
      SystemProgram.transfer({
        fromPubkey: publicKey,
        toPubkey: publicKey,
        lamports: 1000,
      })
    );

    // Кодируем транзакцию в base64
    const serializedTx = Buffer.from(transaction.serialize({ requireAllSignatures: false })).toString('base64');
    
    // Deep link для подписи транзакции
    const signUrl = `phantom://sign/${serializedTx}?redirect=${encodeURIComponent(redirectUrl.toString())}`;
    
    window.location.href = signUrl;
  }, [publicKey]);

  return (
    <div style={{ padding: '20px', maxWidth: '600px', margin: '0 auto' }}>
      <h1 style={{ textAlign: 'center' }}>Phantom Wallet Connection</h1>
      
      {error && (
        <div style={{ color: 'red', margin: '10px 0', padding: '10px', border: '1px solid red' }}>
          {error}
        </div>
      )}

      {!publicKey ? (
        <button
          onClick={connectWallet}
          style={{
            padding: '12px 24px',
            fontSize: '16px',
            backgroundColor: '#512da8',
            color: 'white',
            border: 'none',
            borderRadius: '8px',
            cursor: 'pointer',
            width: '100%'
          }}
        >
          Connect Phantom Wallet
        </button>
      ) : (
        <div>
          <div style={{ 
            margin: '20px 0',
            padding: '15px',
            backgroundColor: '#f0f0f0',
            borderRadius: '8px'
          }}>
            <p style={{ margin: '0 0 5px 0' }}><strong>Connected Wallet:</strong></p>
            <p style={{ 
              wordBreak: 'break-all',
              fontFamily: 'monospace',
              margin: '0'
            }}>
              {publicKey.toBase58()}
            </p>
          </div>

          <button
            onClick={handleTransaction}
            style={{
              padding: '12px 24px',
              fontSize: '16px',
              backgroundColor: '#4caf50',
              color: 'white',
              border: 'none',
              borderRadius: '8px',
              cursor: 'pointer',
              width: '100%',
              marginBottom: '10px'
            }}
          >
            Sign Test Transaction
          </button>
        </div>
      )}

      {txSignature && (
        <div style={{ 
          marginTop: '20px',
          padding: '15px',
          backgroundColor: '#e8f5e9',
          borderRadius: '8px'
        }}>
          <p style={{ margin: '0 0 5px 0' }}><strong>Transaction Signature:</strong></p>
          <p style={{ 
            wordBreak: 'break-all',
            fontFamily: 'monospace',
            margin: '0 0 10px 0'
          }}>
            {txSignature}
          </p>
          <a
            href={`https://explorer.solana.com/tx/${txSignature}?cluster=devnet`}
            target="_blank"
            rel="noopener noreferrer"
            style={{ color: '#2196f3' }}
          >
            View on Solana Explorer
          </a>
        </div>
      )}
    </div>
  );
};

export default App;